@import url(https://fonts.googleapis.com/css2?family=Oranienbaum&family=Prompt:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.brew{display:flex;justify-content:center;align-items:center}.brew button{font-size:2.75rem;text-transform:uppercase}.brew .text{display:inline-flex;justify-content:space-between;align-items:baseline;width:9.5rem;margin-bottom:1rem}.brew .text label{border-bottom:solid 2px transparent}.brew .count{display:inline-block;margin-left:.15ch;font-size:5rem;color:#5F5E5E;position:relative}.brew .count.number-1{margin-right:.25ch;position:relative;top:2px}.selected .text label{border-color:rgba(255,119,37,0.52)}

.controls{display:flex;justify-content:space-between;align-items:center;padding:0 5vw}.controls button{-webkit-appearance:none;-moz-appearance:none;appearance:none;display:flex;justify-content:center;align-items:center;width:5rem;height:5rem;position:relative;top:-.5rem;border:none;border-radius:100%;background-color:white;border:1px solid #A0A0A0;color:#5F5E5E;font-family:'Oranienbaum', serif;font-size:1.6rem}.controls button span{display:inline-block;margin-top:2rem;pointer-events:none}

.color{display:grid;grid-template-rows:25% 25% 25% 25%;grid-template-columns:auto;color:#c7c4c4}.color .active{color:#5F5E5E;transition:color 100ms ease-out}.color .oo{letter-spacing:-.777rem;margin-right:.74rem}.color .text{display:inline-block;font-family:'Prompt', serif;text-transform:uppercase;font-size:1.8rem}

.input--temperature{width:100%;height:75%;position:absolute}.temperature{display:grid;grid-template-areas:"a" "value" "label";grid-template-columns:1fr;grid-template-rows:1fr 1fr 1fr;position:relative}.temperature .value{grid-area:value;display:flex;justify-content:center;align-items:center;line-height:1.1}.temperature .value .amount{font-size:4.5rem}.temperature .label{grid-area:label;display:flex;justify-content:center;align-items:center}.temperature .label .amount-label:first-child{width:50%;margin-right:.25rem}.temperature .label .amount-label:last-child{width:50%;margin-left:0.25rem}.circle{grid-area:value;display:flex;justify-content:center;align-items:flex-end;min-width:8.5rem;min-height:5.5rem;border:2px solid #A0A0A0;border-top:none;border-radius:100%;margin-bottom:0.5rem}.selected .circle{border-color:rgba(255,119,37,0.52)}

.timer{width:150px;margin:0 auto}.input--timer{width:100%;height:100%;position:absolute}.capsule{display:flex;justify-content:space-between;align-items:center;margin:0 auto;width:9rem;padding:0;padding-left:.5rem;padding-right:1rem;border:2px solid #A0A0A0;border-radius:500px;position:relative}.capsule .label{font-size:1.1rem}.values{display:inline-flex;justify-content:space-between;align-items:center;width:4.8rem}.values .value{line-height:1.05}.value{display:inline-block;min-width:1ch;height:4.5rem;font-size:4.5rem;text-align:center;line-height:1}.sec{position:relative;top:-2px}.selected .capsule{border-color:rgba(255,119,37,0.52)}

.name.start .name__name{color:#c7c4c4;border-bottom:1px solid #c7c4c4}.name .name__name{font-size:2.5rem;border-bottom:1px solid #5F5E5E}.selected .name__name{border-color:rgba(255,119,37,0.52) !important}

@keyframes done{0%{opacity:0}9%{opacity:1}35%{opacity:1;background-color:rgba(245,164,115,0.15)}100%{opacity:1}}@keyframes out{0%{opacity:1}100%{opacity:0}}.overlay{width:calc(100% - 22px);height:calc(94% - 23px);position:absolute;border:solid 11px #f5a473;border-top-width:8px;opacity:0;z-index:20;pointer-events:none}.overlay.done{animation:done 8.0s forwards;pointer-events:auto}.overlay.out{animation:out 300ms}

.weight{display:flex;justify-content:center;align-items:center;position:relative}.input--weight{width:100%;height:75%;margin-top:-15%;position:absolute}.diamond{grid-area:diamond;display:flex;justify-content:center;align-items:center;width:6rem;height:6rem;border:2px solid #A0A0A0;transform-origin:center;transform:rotate(45deg);position:absolute;pointer-events:none}.diamond .value{font-size:4.5rem;transform:rotate(-45deg)}.diamond .tsp,.diamond .g{position:absolute;pointer-events:all}.diamond .tsp{transform:rotate(-45deg);top:6.6rem}.diamond .g{transform:translate(4.2rem, .4rem) rotate(-45deg)}.selected .diamond{border-color:rgba(255,119,37,0.52)}

.input--water{width:100%;height:75%;position:absolute}.water{display:grid;grid-template-areas:"a" "value" "label";grid-template-columns:1fr;grid-template-rows:1fr 1fr 1fr;position:relative}.water .value{grid-area:value;display:flex;justify-content:center;align-items:center;line-height:1.1}.water .value .amount{font-size:4.5rem}.water .label{grid-area:label;display:flex;justify-content:center;align-items:center}.water .label .amount-label:first-child{width:50%;margin-right:.25rem}.water .label .amount-label:last-child{width:50%;margin-left:0.25rem}.glass{grid-area:value;display:flex;justify-content:center;align-items:flex-end;min-width:7.5rem;min-height:5.2rem;border:2px solid #A0A0A0;border-top:none;margin-bottom:0.3rem}.selected .glass{border-color:rgba(255,119,37,0.52)}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

html,body{width:100%;height:100%;overflow:hidden;font-family:'Oranienbaum', serif;font-size:16px;color:#5F5E5E}@media screen and (min-width: 425px){html,body{background-color:#fcfffd}}main{width:100%;height:100%;display:grid;grid-template-areas:'controls controls' 'name name' 'color weight' 'gap1 gap1' 'temp water' 'gap2 gap2' 'timer brew' 'gap3 gap3';grid-template-columns:1fr 1fr;grid-template-rows:2% 14% 22% 1% 22% 4% 22% 5%;background-color:white;position:relative}@media screen and (min-width: 425px){main{width:425px;height:100vh;margin:0 auto;border:1px solid #f7f7f7}}input,button{-webkit-appearance:none;-moz-appearance:none;appearance:none;border:none;background:none;font-family:inherit;color:inherit}input:focus,button:focus{outline:none}input{opacity:0;position:absolute}.name{grid-area:name}.controls{grid-area:controls}.name{grid-area:name}.temperature{grid-area:temp}.color{grid-area:color}.weight,.water,.timer,.temperature,.color,.brew{transform-origin:center;transform:scale(0.85);min-width:100%;min-height:100%}.weight{grid-area:weight}.water{grid-area:water}.timer{grid-area:timer}.brew{grid-area:brew}.f-cc{display:flex;justify-content:center;align-items:center}.amount-label{font-size:1.8rem;color:#c7c4c4}.amount-label.active{color:#5F5E5E}

